import React, { Component } from 'react';




export default class portfolio extends Component {
  render() {
    return (
  
        <section id="portfolio">

      <div className="row">

         <div className="twelve columns collapsed">

            <h1>Check Out Some of My Works.</h1>

            {/* --- portfolio wrapper */}
            <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">

          	   <div className="columns portfolio-item">
                  <div className="item-wrap">

                     <a href="#modal-01" title="">
                        <img alt="" src="images/portfolio/NN2.jfif"/>
                        <div className="overlay">
                           <div className="portfolio-item-meta">
          					      <h5>Reccurent Neural Network</h5>
                              <p>Neural Network</p>
          					   </div>
                        </div>
                        <div className="link-icon"><i className="icon-plus"></i></div>
                     </a>

                  </div>
          		</div> {/* item end */}

               <div className="columns portfolio-item">
                  <div className="item-wrap">

                     <a href="#modal-02" title="">
                        <img alt="" src="images/portfolio/node-react.jpeg"/>
                        <div className="overlay">
                           <div className="portfolio-item-meta">
          					      <h5>React and Node</h5>
                              <p>Web Development</p>
          					   </div>
                        </div>
                        <div className="link-icon"><i className="icon-plus"></i></div>
                     </a>

                  </div>
          		</div> {/* item end*/}

               <div className="columns portfolio-item">
                  <div className="item-wrap">

                     <a href="#modal-03" title="">
                        <img alt="" src="images/portfolio/gan.jpg"/>
                        <div className="overlay">
                           <div className="portfolio-item-meta">
          					      <h5>Generative Adversial Network</h5>
                              <p>Neural Network</p>
          					   </div>
                        </div>
                        <div className="link-icon"><i className="icon-plus"></i></div>
                     </a>

                  </div>
          		</div> {/* item end */}

               <div className="columns portfolio-item">
                  <div className="item-wrap">

                     <a href="#modal-04" title="">
                        <img alt="" src="images/portfolio/ml.jpeg"/>
                        <div className="overlay">
                           <div className="portfolio-item-meta">
          					      <h5>Denosing Time Series Data</h5>
                              <p>Machine Learning</p>
          					   </div>
                        </div>
                        <div className="link-icon"><i className="icon-plus"></i></div>
                     </a>

                  </div>
          		</div> {/* item end */}


            </div> {/* protfolio wrapper end */}

         </div> {/*twelve column ends*/}


         {/* modal -pop up
            -----------------------------------------------------------------*/}

         <div id="modal-01" className="popup-modal mfp-hide">

		      <img className="scale-with-grid" src="images/portfolio/modals/m-origami.jpg" alt="" />

		      <div className="description-box">
			      <h4>Data Prediction</h4>
			      <p>To forecast by training time series data on recurent neural network.</p>
               <span className="categories"><i className="fa fa-tag"></i>Neural Network</span>
		      </div>

            <div className="link-box">
               <a href="https://github.com/shafkat2/Recurrent_Neural_Network">Details</a>
		         <a className="popup-modal-dismiss">Close</a>
            </div>

	      </div>{/* modal end 01*/}

         <div id="modal-02" className="popup-modal mfp-hide">

		      <img className="scale-with-grid" src="images/portfolio/modals/hotel.jpg" alt="" />

		      <div className="description-box">
			      <h4>Hotel Booking App</h4>
			      <p>To create an airbnb clone form react and node.</p>
               <span className="categories"><i className="fa fa-tag"></i>Web Development</span>
		      </div>

            <div className="link-box">
               <a href="https://github.com/shafkat2/React-nodejs-app">Details</a>
		         <a className="popup-modal-dismiss">Close</a>
            </div>

	      </div>{/* modal end 02 */}

         <div id="modal-03" className="popup-modal mfp-hide">

		      <img className="scale-with-grid" src="images/portfolio/modals/cls_signal.jpg" alt="" />

		      <div className="description-box">
			      <h4>Signal Processing</h4>
			      <p>To train adversial neural network to generate clean data from nosiy data.</p>
               <span className="categories"><i className="fa fa-tag"></i>Neural Network</span>
		      </div>

            <div className="link-box">
               <a href="https://github.com/shafkat2/Generative_neural_network">Details</a>
		         <a className="popup-modal-dismiss">Close</a>
            </div>

	      </div>{/* modal end -04 */}

         <div id="modal-04" className="popup-modal mfp-hide">

		      <img className="scale-with-grid" src="images/portfolio/modals/m-farmerboy.jpg" alt="" />

		      <div className="description-box">
			      <h4>Detect Refill</h4>
			      <p>We used unsupervised learning and wavelet analysis to clean and detect car fuel refills.</p>
               <span className="categories"><i className="fa fa-tag"></i>Machine Learning</span>
		      </div>

            <div className="link-box">
               <a href="#">Details</a>
		         <a className="popup-modal-dismiss">Close</a>
            </div>

	      </div>{/* modal end -04 */}

      </div> 
        {/* end portfolio*/}
   </section> 
     
    );
  }
}