import React, { Component } from 'react';




export default class contactus extends Component {
  render() {

    const aboutus = this.props.aboutus;
    return (
  
        <section id="contact">

        <div className="row section-head">

           <div className="two columns header-col">

              <h1><span>Get In Touch.</span></h1>

           </div>

           <div className="ten columns">

                 <p className="lead">
                    If you wish to knock or message me please contact me here. 
                 </p>

           </div>

        </div>

        <div className="row">

           <div className="eight columns">

              {/* form */}
              <form action="" method="post" id="contactForm" name="contactForm">
                   <fieldset>

                 <div>
                          <label for="contactName">Name <span className="required">*</span></label>
                          <input type="text" value="" size="35" id="contactName" name="contactName"></input>
                 </div>

                 <div>
                          <label for="contactEmail">Email <span className="required">*</span></label>
                          <input type="text" value="" size="35" id="contactEmail" name="contactEmail"></input>
                 </div>

                 <div>
                          <label for="contactSubject">Subject</label>
                          <input type="text" value="" size="35" id="contactSubject" name="contactSubject"></input>
                 </div>

                 <div>
                    <label for="contactMessage">Message <span className="required">*</span></label>
                    <textarea cols="50" rows="15" id="contactMessage" name="contactMessage"></textarea>
                 </div>

                 <div>
                    <button className="submit">Submit</button>
                    <span id="image-loader">
                       <img alt="" src="images/loader.gif"/>
                    </span>
                 </div>

                   </fieldset>
                  </form> {/* form end */}

              {/* contact warning */}
              <div id="message-warning"> Error boy</div>
              {/* contact success */}
                  <div id="message-success">
                 <i className="fa fa-check"></i>Your message was sent, thank you!<br></br>
                  </div>

           </div>


           <aside className="four columns footer-widgets">

              <div className="widget widget_contact">

                      <h4>Address and Phone</h4>
                      <p className="address">
                          {aboutus.name}<br></br>
                          {aboutus.address1} <br></br>
                          {aboutus.address2}<br></br>
                          <span>{aboutus.number}</span>
                      </p>

                  </div>

              

           </aside>

     </div>
            {/* contactr section ends */}
    </section> 
    );
  }
}