import React, { Component } from 'react';




export default class header extends Component {
  render() {
    const resumeData = this.props.resumeData;
    
    return (
        
        <header id="home">
     
           <nav id="nav-wrap">
     
              <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
               <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
     
              <ul id="nav" class="nav">
                 <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
                 <li><a className="smoothscroll" href="#about">About</a></li>
                  <li><a className="smoothscroll" href="#resume">Resume</a></li>
                 <li><a className="smoothscroll" href="#portfolio">Works</a></li>
                 <li><a className="smoothscroll" href="#testimonials">Testimonials</a></li>
                 <li><a className="smoothscroll" href="#contact">Contact</a></li>
              </ul>     
           </nav> 
     
           <div className="row banner">
              <div className="banner-text">
                 <h1 className="responsive-headline">I'm {resumeData.name}</h1>
                 <h3>I'm a <span>{resumeData.role}</span> planning to make a difference in bangladesh 
                 and all over to the world. Let's <a class="smoothscroll" href="#about">start scrolling </a>
                 and learn more <a class="smoothscroll" href="#about">about me</a>.</h3>
                 <hr />
                 <ul className="social">
                    <li><a href="https://www.linkedin.com/in/shafkat-waheed-417709138/"><i className="fa fa-linkedin"></i></a></li>
                    <li><a href="https://github.com/shafkat2"><i class="fa fa-github"></i></a></li>
                 </ul>
              </div>
           </div>
     
           <p className="scrolldown">
              <a className="smoothscroll" href="#about"><i class="icon-down-circle"></i></a>
           </p>
     
        </header> 
     
    );
  }
}