import React, { Component } from 'react';




export default class about extends Component {
  render() {
     const aboutus = this.props.aboutus;
    return (
  
      <section id="about">

            <div className="row">

               <div className="three columns">

                  <img className="profile-pic"  src="images/my_pic.jpg" alt="" />

               </div>

               <div class="nine columns main-col">

                  <h2>About Me</h2>

                  <p>{aboutus.aboutme}
                  </p>

                  <div class="row">

                     <div class="columns contact-details">

                        <h2>Contact Details</h2>
                        <p class="address">
                           <span>{aboutus.name}</span><br></br>
                           <span>{aboutus.address1}<br></br>
                                 {aboutus.address2}
                           </span><br></br>
                           <span>{aboutus.number}</span><br></br>
                           <span>{aboutus.email}</span>
                        </p>

                     </div>

                     <div class="columns download">
                        <p>
                           <a href="https://drive.google.com/file/d/1jVGvpOzAyD32yT_WWvVCLxbuZVRq-H3P/view" class="button"><i class="fa fa-download"></i>Download Resume</a>
                        </p>
                     </div>

                  </div> 

               </div> 

            </div>

   </section> 
     
    );
  }
}